.articles-slider {
	position: relative;

	&_main {
		margin-bottom: 150px;
	}

	&__title {
		margin-bottom: 55px;
	}

	&__content {
		position: relative;
	}

	.swiper-container {
		margin: 0 -50px;
	}

	.swiper-slide {
		width: 33.333%;
		padding: 0 50px;
	}

	&__arrows {
		display: flex;
		justify-content: space-between;
		position: absolute;
		top: calc(250px / 2);
		left: 50%;
		transform: translate(-50%, -50%);
		width: 100%;
		max-width: 1250px;
		padding: 0 10px;
	}

	&__btn {
		margin-top: 70px;
		text-align: center;
	}

	@media (--phone) {
		&_main {
			margin-bottom: 80px;
		}
	
		&__title {
			margin-bottom: 36px;
		}

		.swiper-container {
			margin: 0 -20px;
		}
	
		.swiper-slide {
			width: 290px;
			padding: 0 20px;
		}
	
		&__arrows {
			display: none;
		}

		&__btn {
			margin-top: 50px;
		}
	}

	@media (max-width: 400px) {
		.swiper-container {
			margin: 0 -10px;
		}
	
		.swiper-slide {
			width: 270px;
			padding: 0 10px;
		}
	}
}