.article-block {
	width: 100%;

	&__pic {
		position: relative;
		display: flex;
		width: 100%;
		max-height: 250px;
		overflow: hidden;
		margin-bottom: 26px;

		&::before {
			content: '';
			padding-top: 100%;
		}

		a {
			position: absolute;
			top: 0;
			left: 0;
		}

		a,
		picture,
		img {
			display: block;
			width: 100%;
			height: 100%;
		}

		img {
			object-fit: cover;
			object-position: center;
		}
	}

	&__title {
		font-size: 22px;
		font-weight: 500;
		margin: 0 0 14px;
	}

	&__date {
		color: #9a9793;
		font-size: 12px;
		margin-bottom: 20px;
	}

	@media (--phone) {
		&__title {
			font-size: 20px;
		}
	}

	@media (max-width: 400px) {
		&__pic {
			margin-bottom: 18px;
		}

		&__title {
			font-size: 16px;
		}

		&__date {
			margin-bottom: 14px;
		}
	}
}